.buttons {
    padding: 0;
    margin: 0;
    text-decoration: none;
    cursor: pointer;
    display: block;
    border-style: none;
    border-radius: 20px;
    min-width: 102px;
    outline: 0;
    padding: 7px 10px;
    font-weight: 400;
    line-height: 1.3;
    transition: 250ms;
}